@charset "UTF-8";
/*sass变量*/
.ac {
  width: 1200px;
  margin: 0 auto; }

.yuele-banner {
  height: 440px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat; }
  .yuele-banner .ac {
    position: relative;
    height: 440px; }
  .yuele-banner .transfer-accounts {
    position: relative;
    position: absolute;
    left: 30px;
    top: 210px;
    width: 200px;
    height: 75px;
    background: #131937;
    border-radius: 20px;
    padding: 10px 20px;
    z-index: 9; }
    .yuele-banner .transfer-accounts p {
      color: #95959C;
      font-size: 14px; }
      .yuele-banner .transfer-accounts p span {
        color: #fff; }
      .yuele-banner .transfer-accounts p i {
        display: inline-block;
        vertical-align: middle;
        width: 15px;
        height: 15px;
        background: url("../images/sx.png") no-repeat center center;
        margin-left: 5px;
        cursor: pointer; }
    .yuele-banner .transfer-accounts h3 {
      display: inline-block;
      width: 40px;
      height: 25px;
      border: 1px solid #1b7394;
      border-radius: 2px;
      padding: 0;
      line-height: 23px;
      text-align: center;
      color: #1b7394;
      font-size: 14px;
      position: absolute;
      top: 50%;
      margin-top: -12.5px;
      right: 24px;
      cursor: pointer; }
      .yuele-banner .transfer-accounts h3:hover {
        border-color: #ff6a66;
        background-color: #ff6a66;
        color: #fff; }
  .yuele-banner .banner-con {
    height: 440px;
    width: 100%;
    position: relative;
    position: absolute;
    left: 0;
    top: 0; }
    .yuele-banner .banner-con li {
      opacity: 0; }
      .yuele-banner .banner-con li.on {
        opacity: 1; }
    .yuele-banner .banner-con .up {
      position: absolute;
      left: 0;
      top: 25px; }
    .yuele-banner .banner-con .down {
      position: absolute;
      right: 0;
      bottom: 0; }

.yuele-banner-list {
  position: relative;
  z-index: 999;
  margin-top: -12px; }
  .yuele-banner-list li {
    float: left;
    height: 108px;
    width: 210px;
    position: relative;
    border-radius: 2px;
    background-color: rgba(255, 255, 255, 0.5);
    cursor: pointer;
    margin-right: 30px; }
    .yuele-banner-list li img {
      position: absolute;
      left: 50%;
      bottom: 38px; }
    .yuele-banner-list li p {
      position: absolute;
      width: 100%;
      left: 0;
      bottom: 0;
      height: 38px;
      line-height: 38px;
      text-align: center;
      overflow: hidden;
      background-color: rgba(21, 148, 203, 0.9);
      color: #fff; }
    .yuele-banner-list li.on, .yuele-banner-list li:hover {
      background-color: rgba(59, 169, 216, 0.8); }
      .yuele-banner-list li.on p, .yuele-banner-list li:hover p {
        font-size: 18px; }
  .yuele-banner-list .li1 img {
    margin-left: -66px; }
  .yuele-banner-list .li2 img {
    margin-left: -66px; }
  .yuele-banner-list .li3 img {
    margin-left: -66px; }
  .yuele-banner-list .li4 img {
    margin-left: -66px; }
  .yuele-banner-list .li5 img {
