.m-mask {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 9998;
  display: none; }

.dialog-accounts {
  position: relative;
  position: absolute;
  left: 50%;
  top: 50%;
  background: #fff;
  height: 520px;
  width: 1000px;
  margin-left: -500px;
  margin-top: -260px;
  z-index: 9999;
  display: none; }
  .dialog-accounts .hd {
    height: 40px;
    line-height: 40px;
    padding: 0 20px;
    background-color: #0078bd; }
    .dialog-accounts .hd h2 {
      font-size: 16px;
      font-weight: 400;
      color: #fff;
      float: left; }
    .dialog-accounts .hd i {
      float: right;
      height: 40px;
      width: 40px;
      background: url("../images/close.png") no-repeat center center;
      background-size: 20px auto;
      cursor: pointer; }
  .dialog-accounts .con {
    padding: 30px 20px 20px; }
  .dialog-accounts .bd {
    height: 100px; }
    .dialog-accounts .bd li {
      width: 150px;
      float: left;
      margin-right: 10px;
      background: #f0f0f0;
      border: 1px solid #d1d1d1;
      height: 100px;
      text-align: center;
      padding-top: 15px; }
      .dialog-accounts .bd li p {
        line-height: 20px; }
      .dialog-accounts .bd li h3 {
        line-height: 40px;
        font-weight: 400;
        color: #223853;
        font-size: 24px; }
      .dialog-accounts .bd li i {
        display: inline-block;
        vertical-align: middle;
        width: 15px;
        height: 15px;
        background: url("../images/sx.png") no-repeat center center;
        cursor: pointer;
        margin-top: -5px; }
  .dialog-accounts .ft {
    margin-top: 20px;
    margin-left: 30px; }
    .dialog-accounts .ft li {
      padding-left: 85px;
      position: relative;
      margin-bottom: 10px; }
      .dialog-accounts .ft li .name {
        line-height: 35px;
        position: absolute;
        left: 0;
        top: 0;
        width: 80px;
        text-align: right; }
      .dialog-accounts .ft li .radio {
        line-height: 35px; }
        .dialog-accounts .ft li .radio input,
        .dialog-accounts .ft li .radio label {
          display: inline-block;
          vertical-align: middle; }
      .dialog-accounts .ft li .select select {
        height: 30px;
        width: 150px;
        margin-top: 2.5px; }
      .dialog-accounts .ft li .input input {
        display: inline-block;
        border-radius: 2px;
        background-color: #f7f7f7;
        height: 35px;
        width: 280px;
        color: #333333;
        font-size: 14px;
        position: relative;
        border: 1px solid #cccccc;
        padding: 5px 10px;
        line-height: 23px; }
      .dialog-accounts .ft li .submit input {
        background-color: #1594cb;
        border: none;
        padding: 0;
        width: 135px;
        height: 35px;
        border-radius: 3px;
        line-height: 35px;
        text-align: center;
        color: #fff;
        font-size: 14px;
        cursor: pointer; }
        .dialog-accounts .ft li .submit input:hover {
          opacity: 0.8; }
  .dialog-accounts .tips {
    border: 1px solid #f1e5af;
    border-radius: 2px;
    background-color: #fbf7d8;
    padding: 2px 5px; }
    .dialog-accounts .tips img,
    .dialog-accounts .tips span {
      display: inline-block;
      vertical-align: middle;
      color: #8c8c8c;
      font-family: 宋体;
      font-size: 12px; }

